(function() {
  angular.module('whitbread').service('venuesSvc', function($http, toastr, rootUrl, filter, amount, loc, query, client_id, client_secret) {
    'ngInject';
    var _self;
    _self = this;
    this.getPopularVenues = function(city, n, venue) {
      var promise;
      return promise = $http.get(rootUrl + filter + city + '&' + amount + n + '&' + client_id + '&' + client_secret + '&' + loc + '&' + query + venue)["catch"](function(error) {
        return toastr.error('There was an error in your request, please try changing the name of the city');
      });
    };
  });

}).call(this);



(function() {
  angular.module('whitbread').directive('acmeNavbar', function() {
    var NavbarController, directive;
    NavbarController = function() {
      'ngInject';
      var vm;
      vm = this;
    };
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };
  });

}).call(this);



(function() {
  angular.module('whitbread').directive('acmeMalarkey', function() {
    var MalarkeyController, directive, linkFunc;
    MalarkeyController = function() {
      'ngInject';
      var vm;
      vm = this;
      vm.statement = ['With â™¥', 'Thanks for the opportunity of showing my skills'];
    };
    linkFunc = function(scope, el, attr, vm) {
      var typist, watcher;
      typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });
      el.addClass('acme-malarkey');
      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause()["delete"]();
      });
      watcher = scope.$watch('vm.statement', function() {
        angular.forEach(vm.statement, function(sentence) {
          typist.type(sentence).pause()["delete"]();
        });
      });
      scope.$on('$destroy', function() {
        watcher();
      });
    };
    return directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };
  });

}).call(this);



(function() {
  angular.module('whitbread').controller('VenuesController', function(venuesSvc) {
    'ngInject';
    var fetchVenues, vm;
    vm = this;
    fetchVenues = function() {
      vm.loading = true;
      vm.displayNoResults = false;
      return venuesSvc.getPopularVenues(vm.city, vm.number, vm.venueType).then(function(result) {
        vm.loading = false;
        if (result.data) {
          vm.venues = result.data.response.venues;
          return vm.displayNoResults = vm.venues.length === 0;
        } else {
          return vm.displayNoResults = true;
        }
      });
    };
    vm.loading = false;
    vm.displayNoResults = false;
    vm.numbers = [10, 20, 50, 100];
    vm.fetchVenues = fetchVenues;
  });

}).call(this);



(function() {
  angular.module('whitbread').controller('MainController', function($timeout, $location, webDevTec, toastr) {
    'ngInject';
    var activate, getWebDevTec, goToVenues, vm;
    vm = this;
    activate = function() {
      getWebDevTec();
      $timeout((function() {
        vm.classAnimation = 'rubberBand';
      }), 4000);
    };
    goToVenues = function() {
      $location.path('/venues');
      vm.classAnimation = '';
    };
    getWebDevTec = function() {
      vm.techStacks = webDevTec.getTec();
      angular.forEach(vm.techStacks, function(stack) {
        stack.rank = Math.random();
      });
    };
    vm.techStacks = [];
    vm.classAnimation = '';
    vm.goToVenues = goToVenues;
    activate();
  });

}).call(this);



