(function() {
  angular.module('whitbread').config(function($routeProvider) {
    'ngInject';
    return $routeProvider.when('/', {
      templateUrl: 'app/main/main.html',
      controller: 'MainController',
      controllerAs: 'main'
    }).when('/venues', {
      templateUrl: 'app/venues/venues.html',
      controller: 'VenuesController',
      controllerAs: 'main'
    }).otherwise({
      redirectTo: '/'
    });
  });

}).call(this);



